import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import(/* webpackChunkName: "about" */ '../views/Home.vue')
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/usuario',
    name: 'Usuario',
    component: () => import(/* webpackChunkName: "usuario" */ '../views/User.vue'),
    meta: { verificarToken: true, verificarRol: 'ADMIN', verificar: 'EMP' }
  },
  {
    path: '/game',
    name: 'Game',
    component: () => import(/* webpackChunkName: "game" */ '../views/Game.vue'),
    meta: { verificarToken: true, verificarRol: 'ADMIN' }

  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "login" */ '../views/Login.vue')
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import(/* webpackChunkName: "register" */ '../views/Register.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  const rutaSegura = to.matched.some(record => record.meta.verificarToken);
  const rolSeguro = to.matched.some(record => record.meta.verificarRol);
  const emp = to.matched.some(record => record.meta.verificar);
  //validar que el usuario tenga un token
  if (rutaSegura && store.state.token === '') {
    next({ name: 'Home' })
  } else {
    if (rolSeguro && store.state.rolUser === 'USER') {
      alert('No tienes la experiencia necesaria para entrar aqui!');
      next({ name: 'Home' })
    } else if (emp && store.state.rolUser === 'EMP') {
      alert('ACCESO DENEGADO ROL: '+store.state.rolUser);
      next({ name: 'Home' })
    } else {
      next()
    }
  }
  //validar el rol del usuario

})
export default router