import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router/index'
import jwt from 'jsonwebtoken'

import decode from 'jwt-decode'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: '',
    userDB: '',
    rolUser: '',
    userR: '',
    _id: ''
  },
  mutations: {
    obtenerUsuario(state, payload) {
      state.token = payload;
      if (payload === '') {
        state.userDB = ''
      } else {
        state.userDB = decode(payload)
      }
    },
    obtenerRol(state, payload) {
      state.rolUser = payload;
      if (payload === '') {
        state.userR = ''
      } else {
        state.userR = payload
        router.push({ name: 'Home' })        
      }
    }
  },
  actions: {
    //Token
    guardarUsuario({ commit }, payload) {
      localStorage.setItem('token', payload);
      commit('obtenerUsuario', payload)
    },
    cerrarSesion({ commit }) {
      commit('obtenerUsuario', '');
      localStorage.removeItem('token');
      //localStorage.removeItem('rolUsuario')
      router.push({ name: 'Login' });
    },
    leerToken({ commit }) {
      const token = localStorage.getItem('token')
      if (token) {
        commit('obtenerUsuario', token)
      } else {
        commit('obtenerUsuario', '')
      }
    },
    //Rol Usuario
    guardarRol({ commit }, payload) {
      //localStorage.setItem('rolUsuario', payload);
      commit('obtenerRol', payload)
    },
    leerRol({ commit }) {
      const token = jwt.decode(localStorage.getItem('token'))
      const rol = token.role
      const id = token.id
      this.state._id = id
      if (rol === 'ADMIN') {
        commit('obtenerRol', rol)
      } else if(rol === 'EMP') {
        commit('obtenerRol', rol)
      } else {
        commit('obtenerRol', rol)
      }
    }
  },
  getters: {
    estaActivo: state => !!state.token,
    siMostrar: state => !!state.rolUser
  }
})
